/* css for Missions */
/* Hacking missions */
#mission-container {
  overflow: hidden; }

.hack-mission-grid {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-gap: 2.5%;
  height: 90%;
  position: absolute;
  width: 100%;
  overflow-y: auto;
  padding-right: 10px; }
  .hack-mission-grid::-webkit-scrollbar {
    display: none; }

.hack-mission-node {
  z-index: 5;
  background-color: #808080;
  align-self: center;
  justify-self: center;
  display: inline-block; }
  .hack-mission-node p {
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    margin-top: 8px;
    color: #fff;
    font-size: 12px;
    text-align: center; }

.hack-mission-player-node {
  color: #fff;
  background-color: #00f; }

.hack-mission-player-node-active {
  border: 2px solid #fff;
  background-color: #66f; }

.hack-mission-enemy-node {
  color: #fff;
  background-color: #f00; }

.hack-mission-cpu-node {
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  border-radius: 50%;
  width: 100%;
  height: 100%; }

.hack-mission-firewall-node {
  width: 90%;
  height: 100%; }

.hack-mission-database-node {
  -webkit-transform: skew(20deg);
  -moz-transform: skew(20deg);
  -ms-transform: skew(20deg);
  -o-transform: skew(20deg);
  transform: skew(20deg);
  width: 100%;
  height: 90%; }
  .hack-mission-database-node p {
    -webkit-transform: skew(-20deg);
    -moz-transform: skew(-20deg);
    -ms-transform: skew(-20deg);
    -o-transform: skew(-20deg);
    transform: skew(-20deg);
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    color: #fff;
    font-size: 12px;
    margin-top: 8px;
    text-align: center; }

.hack-mission-transfer-node {
  -webkit-transform: skew(-20deg);
  -moz-transform: skew(-20deg);
  -ms-transform: skew(-20deg);
  -o-transform: skew(-20deg);
  transform: skew(-20deg);
  width: 100%;
  height: 90%; }
  .hack-mission-transfer-node p {
    -webkit-transform: skew(20deg);
    -moz-transform: skew(20deg);
    -ms-transform: skew(20deg);
    -o-transform: skew(20deg);
    transform: skew(20deg);
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    color: #fff;
    font-size: 12px;
    margin-top: 8px;
    text-align: center; }

.hack-mission-spam-node,
.hack-mission-shield-node {
  height: 100%;
  width: 100%; }

/* Non-map related DOM elements */
/* Element at the top of the Hacking Mission page (intro page, start button, guide buttons, etc.) */
.hack-mission-header-element {
  margin: 6px; }

.hack-mission-action-buttons-container {
  border: 2px solid #fff; }
