/* Attributes */
/* interactivetutorial.css */
#interactive-tutorial-wrapper {
  position: relative; }

#interactive-tutorial-container {
  display: none;
  position: absolute;
  /* Stay in place */
  right: 0;
  top: 0;
  height: 450px;
  padding: 10px;
  border: 5px solid #fff;
  width: 23%;
  overflow: hidden;
  background-color: #444;
  /* Fallback color */
  color: #fff; }
  #interactive-tutorial-container > strong {
    background-color: #444; }

#interactive-tutorial-text {
  padding: 4px;
  margin: 4px;
  color: #fff;
  background-color: #444;
  font-size: 14px;
  max-height: 350px;
  overflow-y: auto; }

#interactive-tutorial-exit,
#interactive-tutorial-next,
#interactive-tutorial-back {
  -webkit-border-radius: 12px;
  -moz-border-radius: 12px;
  border-radius: 12px;
  -webkit-box-shadow: 1px 1px 3px #000;
  -moz-box-shadow: 1px 1px 3px #000;
  box-shadow: 1px 1px 3px #000;
  color: #aaa;
  font-size: 18px;
  font-weight: bold;
  background-color: #000; }
  #interactive-tutorial-exit:hover, #interactive-tutorial-exit:focus,
  #interactive-tutorial-next:hover,
  #interactive-tutorial-next:focus,
  #interactive-tutorial-back:hover,
  #interactive-tutorial-back:focus {
    color: #fff;
    text-decoration: none;
    cursor: pointer; }

#interactive-tutorial-exit {
  position: absolute;
  bottom: 0;
  left: 0;
  padding: 4px; }

#interactive-tutorial-back {
  float: left;
  padding: 4px; }

#interactive-tutorial-next {
  float: right;
  padding: 4px; }
