/* Attributes */
/**
 * Styling for the Hacknet Nodes UI Page
 */
#hacknet-nodes-container {
  position: fixed;
  padding: 10px; }

.hacknet-general-info {
  margin: 10px;
  width: 70vw; }

#hacknet-nodes-container li {
  float: left;
  overflow: hidden;
  white-space: nowrap; }
  #hacknet-nodes-container li.hacknet-node {
    -webkit-box-shadow: inset 0 0 8px rgba(0, 0, 0, 0.1), 0 0 16px rgba(0, 0, 0, 0.1);
    -moz-box-shadow: inset 0 0 8px rgba(0, 0, 0, 0.1), 0 0 16px rgba(0, 0, 0, 0.1);
    box-shadow: inset 0 0 8px rgba(0, 0, 0, 0.1), 0 0 16px rgba(0, 0, 0, 0.1);
    margin: 6px;
    padding: 7px;
    width: 35vw;
    border: 2px solid var(--my-highlight-color); }

#hacknet-nodes-list {
  list-style: none;
  width: 82vw; }

#hacknet-nodes-money {
  margin: 10px;
  float: left; }

#hacknet-nodes-money-multipliers-div {
  display: inline-block;
  width: 70vw; }

#hacknet-nodes-multipliers {
  float: right; }

#hacknet-nodes-purchase-button {
  display: inline-block; }

.hacknet-node-container {
  display: inline-table; }
  .hacknet-node-container .row {
    display: table-row;
    height: 30px; }
    .hacknet-node-container .row p {
      display: table-cell; }
  .hacknet-node-container .upgradable-info {
    display: inline-block;
    margin: 0 4px;
    /* Don't want the vertical margin/padding, just left & right */
    padding: 0 4px;
    width: 64px; }
