/* COLORS */
/* Attributes */
/**
 * Styling for the Character Overview Panel (top-right panel)
 */
#character-overview-wrapper {
  position: relative; }

#character-overview-container {
  display: none;
  position: absolute;
  /* Stay in place */
  right: 0;
  top: 0;
  height: auto;
  /* Full height */
  padding: 10px 2px;
  border: 2px solid var(--my-highlight-color);
  width: auto;
  max-width: 280px;
  overflow: auto;
  /* Enable scroll if needed */
  background-color: rgba(57, 54, 54, 0.9);
  /* Fallback color */
  z-index: 1; }

#character-overview-text {
  color: #faffdf; }
  #character-overview-text table {
    border-collapse: collapse;
    margin: auto; }
  #character-overview-text td {
    padding: 2px;
    vertical-align: middle; }

.character-stat-text {
  color: #fff;
  background-color: #444; }

.character-stat-cell {
  text-align: right; }

#character-hack-wrapper td,
#character-agi-wrapper td {
  border-bottom: 1px #aaa solid;
  padding-bottom: 10px; }

#character-str-wrapper td,
#character-cha-wrapper td {
  padding-top: 10px; }

#character-hp-wrapper {
  color: #dd3434; }

#character-money-wrapper {
  color: #ffd700; }

#character-hack-wrapper {
  color: #adff2f; }

#character-cha-wrapper {
  color: #a671d1; }

#character-int-wrapper {
  color: #6495ed; }

.character-overview-btn {
  -webkit-border-radius: 12px;
  -moz-border-radius: 12px;
  border-radius: 12px;
  -webkit-box-shadow: 1px 1px 3px #000;
  -moz-box-shadow: 1px 1px 3px #000;
  box-shadow: 1px 1px 3px #000;
  color: #cecece;
  display: inline-block;
  font-size: 14px;
  font-weight: bold;
  height: 25px;
  background-color: #000;
  padding: 5px 8px; }

.character-quick-options {
  margin-top: 10px;
  text-align: center; }

.character-overview-btn:hover,
.character-overview-btn:focus {
  color: #fff;
  text-decoration: none;
  cursor: pointer; }
