/* COLORS */
/* Attributes */
/* CSS for different main menu pages, such as character info, script editor, etc (but excluding
    terminal which has its own page) */
.generic-menupage-container {
  height: 100%;
  padding-left: 10px;
  margin-left: 10%;
  width: 99%;
  overflow-y: scroll; }

/* Character Info */
#character-container {
  padding-top: 10px;
  position: fixed; }

/* Active scripts */
.active-scripts-list {
  list-style-type: none; }

#active-scripts-container {
  position: fixed;
  padding-top: 10px; }

#active-scripts-text,
#active-scripts-total-prod {
  width: 70%;
  margin: 6px;
  padding: 4px; }

.active-scripts-server-header {
  background-color: #444;
  font-size: 20px;
  color: #fff;
  margin: 6px 6px 0 6px;
  padding: 6px;
  cursor: pointer;
  width: 60%;
  text-align: left;
  border: none;
  outline: none; }

.active-scripts-server-header.active,
.active-scripts-server-header:hover {
  background-color: #555; }

.active-scripts-server-header.active:hover {
  background-color: #666; }

.active-scripts-server-header:after {
  content: '\2795';
  /* "plus" sign (+) */
  font-size: 13px;
  color: #fff;
  float: right;
  margin-left: 5px; }

.active-scripts-server-header.active:after {
  content: "\2796";
  /* "minus" sign (-) */
  font-size: 13px;
  color: #fff;
  float: right;
  margin-left: 5px; }

.active-scripts-server-panel {
  margin: 0 6px 6px 6px;
  padding: 0 6px 6px 6px;
  width: 55%;
  margin-left: 5%;
  display: none; }

.active-scripts-server-panel div,
.active-scripts-server-panel ul,
.active-scripts-server-panel ul > li {
  background-color: #555; }

.active-scripts-script-header {
  background-color: #555;
  color: var(--my-font-color);
  padding: 4px 25px 4px 10px;
  cursor: pointer;
  width: auto;
  text-align: left;
  border: none;
  outline: none;
  position: relative; }
  .active-scripts-script-header:after {
    content: '\2795';
    /* "plus" sign (+) */
    font-size: 13px;
    float: right;
    margin-left: 5px;
    color: transparent;
    text-shadow: 0 0 0 var(--my-font-color);
    position: absolute;
    bottom: 4px; }
  .active-scripts-script-header.active:after {
    content: "\2796";
    /* "minus" sign (-) */ }
  .active-scripts-script-header:hover, .active-scripts-script-header.active:hover {
    background-color: #666; }
  .active-scripts-script-header.active {
    background-color: #555; }

.active-scripts-script-panel {
  padding: 0 18px;
  background-color: #555;
  width: auto;
  display: none;
  margin-bottom: 6px; }
  .active-scripts-script-panel p, .active-scripts-script-panel h2, .active-scripts-script-panel ul, .active-scripts-script-panel li {
    background-color: #555;
    width: auto;
    color: #fff;
    margin-left: 5%; }

/* World */
#world-container {
  position: fixed;
  padding-top: 10px; }

#world-city-name,
#world-city-desc {
  padding: 4px;
  margin: 4px; }

/* Create program */
#create-program-container {
  position: fixed;
  padding-top: 10px; }

#create-program-page-text,
#create-program-list {
  width: 70%; }

/* Factions and Faction (Single Faction page) */
#factions-container {
  position: fixed;
  padding-top: 10px; }

#faction-container {
  position: fixed;
  padding-top: 10px; }

.faction-work-div {
  width: 70%;
  height: 100%; }

.faction-work-div-wrapper {
  overflow: hidden;
  border: 2px solid #333;
  padding: 6px;
  margin: 6px;
  width: 70%; }

#faction-donate-amount-txt,
#faction-donate-input {
  padding: 6px;
  margin: 6px;
  display: inline-block;
  color: var(--my-font-color);
  background-color: #000; }

#faction-donate-amount-txt {
  width: 50%; }

#faction-container p,
#faction-container pre {
  padding: 4px 6px;
  margin: 4px 6px; }

#faction-container pre {
  width: 70%;
  white-space: pre-wrap;
  /* Since CSS 2.1 */
  white-space: -moz-pre-wrap;
  /* Mozilla, since 1999 */
  white-space: -pre-wrap;
  /* Opera 4-6 */
  white-space: -o-pre-wrap;
  /* Opera 7 */
  word-wrap: break-word;
  /* Internet Explorer 5.5+ */ }

/* Faction Augmentations */
#faction-augmentations-container {
  position: fixed;
  padding-top: 10px; }
  #faction-augmentations-container p, #faction-augmentations-container a, #faction-augmentations-container ul, #faction-augmentations-container h1 {
    margin: 8px;
    padding: 4px; }

/* World */
#world-container li {
  margin: 0 0 15px 0;
  list-style-type: none; }

/* Augmentations */
#augmentations-container {
  position: fixed;
  padding-top: 10px; }

.augmentations-list button,
.augmentations-list div {
  color: var(--my-font-color);
  text-decoration: none; }

.augmentations-list button {
  padding: 2px 5px; }

.augmentations-list div {
  padding: 6px; }

/* Tutorial */
#tutorial-container {
  position: fixed;
  padding-top: 10px; }

#tutorial-text {
  width: 70%;
  margin: 10px; }

#tutorial-container a {
  width: 50%; }

/* Dev menu */
#dev-menu-container {
  position: fixed;
  padding-top: 10px; }

#dev-menu-text {
  width: 70%;
  margin: 10px; }

#dev-menu-container a {
  width: 50%; }

/* Location */
#location-container {
  position: fixed;
  padding: 6px;
  overflow-x: hidden; }

#location-container a {
  display: inline-block;
  width: 30%; }

#location-slums-description {
  width: 70%;
  margin: 10px; }

#location-return-to-world-button {
  margin: 10px;
  padding: 6px; }

#location-container > * {
  margin: 10px 5px 10px 5px; }

#location-job-reputation,
#location-company-favor {
  display: inline; }

/* Infiltration */
#infiltration-container {
  position: fixed;
  padding: 6px; }
  #infiltration-container span {
    margin: 0;
    padding: 0; }

#infiltration-left-panel,
#infiltration-right-panel {
  display: inline-block;
  border: 1px solid #fff;
  width: 35%;
  height: 75%;
  top: 10px;
  overflow-y: auto;
  overflow-x: auto; }

#infiltration-faction-select {
  color: #fff; }

#infiltration-left-panel p,
#infiltration-right-panel p {
  margin: 4px; }

#infiltration-buttons {
  margin-top: 20px; }

#infiltration-buttons .a-link-button {
  display: inline;
  width: 25%; }
